/* ============== */
/* CSS Animations */
/* ============== */
@-webkit-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadein {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes zoomin {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 1;
  }
}
@-moz-keyframes zoomin {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 1;
  }
}
@-o-keyframes zoomin {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 1;
  }
}
@keyframes zoomin {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 1;
  }
}
@-webkit-keyframes zoominfaded {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 0.9;
  }
}
@-moz-keyframes zoominfaded {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 0.9;
  }
}
@-o-keyframes zoominfaded {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 0.9;
  }
}
@keyframes zoominfaded {
  from {
    opacity: 0;
    -webkit-transform: scale3d(0.3, 0.3, 0.3);
    -moz-transform: scale3d(0.3, 0.3, 0.3);
    -ms-transform: scale3d(0.3, 0.3, 0.3);
    -o-transform: scale3d(0.3, 0.3, 0.3);
    transform: scale3d(0.3, 0.3, 0.3);
  }
  to {
    opacity: 0.9;
  }
}
@-webkit-keyframes slideindown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@-moz-keyframes slideindown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@-o-keyframes slideindown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
@keyframes slideindown {
  from {
    transform: translate3d(0, -100%, 0);
    visibility: visible;
  }
  to {
    transform: translate3d(0, 0, 0);
  }
}
*,
*:after,
*:before {
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  -webkit-font-smoothing: antialiased;
  font-smoothing: antialiased;
  text-rendering: optimizeLegibility;
}
body {
  font: 16px/1.1 'Roboto', Helvetica, Arial, sans-serif;
  color: #666666;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-family: 'Roboto Condensed', sans-serif;
  color: #000000;
  font-weight: bold;
  margin: 0 0 20px;
  text-transform: uppercase;
}
h1 {
  font-size: 54px;
}
h2 {
  font-size: 50px;
}
h3 {
  font-size: 44px;
}
h4 {
  font-size: 30px;
}
h5 {
  font-size: 28px;
}
p {
  margin: 0 0 10px;
}
/* clearfix */
.clearfix::after {
  content: "";
  clear: both;
  display: table;
}
img {
  max-width: 100%;
  height: auto;
}
a {
  color: #666666;
  text-decoration: none;
}
a:focus {
  outline: 0;
}
a:hover,
a:active {
  outline: 0;
}
input:focus {
  outline: 0;
  border: 1px solid #272727;
}
/*------------------------------------*\
    Buttons
\*------------------------------------*/
.btn {
  display: inline-block;
  background-color: #C61C30;
  color: #FFFFFF;
  font-size: 14px;
  line-height: 18px;
  font-weight: bold;
  padding: 11px 20px;
  text-transform: uppercase;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  -webkit-box-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.1);
  -moz-box-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.1);
  box-shadow: 1px 1px 0 0 rgba(0, 0, 0, 0.1);
  -webkit-transition: background-color 0.3s;
  -moz-transition: background-color 0.3s;
  -ms-transition: background-color 0.3s;
  -o-transition: background-color 0.3s;
  transition: background-color 0.3s;
}
.btn:hover {
  background-color: #9B1D2C;
  color: #FFFFFF;
}
button.btn {
  outline: 0;
  border: none;
}
/*------------------------------------*\
    MISC
\*------------------------------------*/
::selection {
  background: #F7F7F7;
  color: #FFFFFF;
  text-shadow: none;
}
::-webkit-selection {
  background: #F7F7F7;
  color: #FFFFFF;
  text-shadow: none;
}
::-moz-selection {
  background: #F7F7F7;
  color: #FFFFFF;
  text-shadow: none;
}
/*------------------------------------*\
    WORDPRESS CORE
\*------------------------------------*/
.alignnone {
  margin: 5px 20px 20px 0;
}
.aligncenter,
div.aligncenter {
  display: block;
  margin: 5px auto 5px auto;
}
.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}
.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}
a img.alignright {
  float: right;
  margin: 5px 0 20px 20px;
}
a img.alignnone {
  margin: 5px 20px 20px 0;
}
a img.alignleft {
  float: left;
  margin: 5px 20px 20px 0;
}
a img.aligncenter {
  display: block;
  margin-left: auto;
  margin-right: auto;
}
.wp-caption {
  background: #FFF;
  border: 1px solid #F0F0F0;
  max-width: 96%;
  padding: 5px 3px 10px;
  text-align: center;
}
.wp-caption.alignnone {
  margin: 5px 20px 20px 0;
}
.wp-caption.alignleft {
  margin: 5px 20px 20px 0;
}
.wp-caption.alignright {
  margin: 5px 0 20px 20px;
}
.wp-caption img {
  border: 0 none;
  height: auto;
  margin: 0;
  max-width: 98.5%;
  padding: 0;
  width: auto;
}
.wp-caption .wp-caption-text,
.gallery-caption {
  font-size: 11px;
  line-height: 17px;
  margin: 0;
  padding: 0 4px 5px;
}
.header {
  background: #FFFFFF;
  text-align: right;
  padding: 10px 0;
}
.header .header-inner {
  position: relative;
  max-width: 1480px;
  margin: 0 auto;
  padding: 46px 20px 46px 172px;
}
.header .logo {
  position: absolute;
  top: 0;
  left: 20px;
  width: 132px;
  height: auto;
}
.header .nav {
  display: inline-block;
  vertical-align: middle;
}
.header .nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.header .nav ul li {
  display: inline-block;
  vertical-align: top;
  margin: 0 20px 0 0;
  font-size: 16px;
  line-height: 32px;
  font-weight: bold;
  text-transform: uppercase;
}
.header .nav ul li:last-child {
  margin-right: 0;
}
.header .nav ul li a {
  color: #2C2C2C;
}
.header .header-search {
  display: inline-block;
  vertical-align: middle;
  padding: 0 0 0 20px;
  width: 25%;
}
.header .header-search .input-wrap {
  position: relative;
}
.header .header-search .input-wrap input.search-input {
  height: 40px;
  border: 1px solid #272727;
  background-color: #F7F7F7;
  font-size: 13px;
  line-height: 38px;
  color: #696969;
  border-radius: 21px;
  padding: 0 10px;
  width: 100%;
}
.header .header-search .input-wrap .search-submit {
  position: absolute;
  top: 0px;
  right: 0;
  height: 40px;
  width: 40px;
  outline: 0;
  border: none;
}
.footer {
  background: #000000;
  color: #FFFFFF;
  text-align: center;
  padding: 30px 50px;
  font-size: 13px;
  line-height: 16px;
}
.footer a {
  color: #FFFFFF;
}
.footer .footer-content {
  border-bottom: 1px solid rgba(224, 224, 224, 0.4);
  padding-bottom: 15px;
  max-width: 1200px;
  margin: 0 auto 30px;
}
.footer .footer-nav ul {
  list-style: none;
  padding: 0;
  margin: 0;
}
.footer .footer-nav ul li {
  display: inline-block;
  vertical-align: middle;
  margin: 0 20px 0 0;
  font-size: 18px;
  line-height: 20px;
  font-weight: bold;
  text-transform: uppercase;
}
.footer .footer-nav ul li:last-child {
  margin-right: 0;
}
.footer .address {
  margin: 20px 0;
}
.footer .socials {
  list-style: none;
  margin: 0;
  padding: 0;
}
.footer .copyright {
  opacity: 0.66;
  margin: 0;
}
.home .home-banner {
  position: relative;
  background: url(../images/home-banner-lucy.jpg) center center no-repeat;
  background-size: cover;
}
.home .home-banner:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 42.85714286%;
}
.home .home-banner .text-box {
  position: absolute;
  top: 10%;
  right: 10%;
  max-width: 500px;
}
.home .home-banner .text-box h2 {
  font-size: 50px;
  line-height: 58px;
  color: #FFFFFF;
  font-weight: bold;
  margin: 0 0 10px;
  text-transform: none;
}
.home .home-banner .text-box p {
  margin: 0 0 20px;
  font-family: 'Roboto Condensed', sans-serif;
  font-size: 28px;
  line-height: 33px;
  font-weight: bold;
  color: #696969;
}
.home .home-banner .text-box .btn {
  padding-left: 60px;
  padding-right: 60px;
}
.home .welcome-text {
  text-align: center;
  color: #FFFFFF;
  background-color: #2C2C2C;
  padding: 60px 20px;
}
.home .welcome-text .welcome-text-inner {
  max-width: 880px;
  margin: 0 auto;
}
.home .welcome-text h2 {
  color: #FFFFFF;
}
.home .welcome-text p {
  font-size: 20px;
  line-height: 32px;
}
.home .welcome-text p:first-of-type {
  margin-bottom: 20px;
}
.home .available-dogs-container {
  text-align: center;
  padding: 40px 20px;
}
.home .available-dogs-container h2 {
  color: #C61C30;
}
.home .available-dogs-container p.subtitle {
  color: #000000;
  font-size: 14px;
  margin-bottom: 30px;
}
.home .available-dogs-container p.subtitle a {
  color: #000000;
  text-decoration: underline;
}
.home .available-dogs-container .available-dogs {
  list-style: none;
  max-width: 1200px;
  margin: 0 auto;
  padding: 0;
}
.home .available-dogs-container .available-dogs li {
  float: left;
  width: 32%;
  text-align: center;
  margin-right: 2%;
  margin-bottom: 2%;
}
.home .available-dogs-container .available-dogs li:nth-child(3n) {
  margin-right: 0;
}
